	module.exports = {"data":{"markdownRemark":{"html":"<p>The most interesting songs always take a few listens to reveal themselves.\nThe hard part is getting a person to listen enough for that to happen.\nWriting a great album requires a balance between songs which pull listeners in, and songs which keep their attention.\n\"Honky Tonk Heroes\" is a great example of this sort of thing.</p>\n<p>You are pulled in by the title track, and \"Low Down Freedom\".\n\"Old Five and Dimers Like Me\", and \"Ain't No God In Mexico\" keep you coming back for more.\n\"<a href=\"/songs/willie-the-wandering-gypsy\">Willie the Wandering Gypsy</a>\" changes your life, and perception of the world.</p>\n<p>\"<a href=\"/songs/willie-the-wandering-gypsy\">Willie the Wandering Gypsy and Me</a>\" resembles M.C. Escher's sphere drawings.\nThe spheres where drawn in the first person perspective by Escher himself.\nWithin the reflection of the sphere is Escher, and the world around him.\nIn the one, his eyes reflect back the sphere, reflecting back on itself.\nIn another his hand is shown drawing the painting you are looking at, again reflecting back on itself.</p>\n<p>Like Escher's drawing, the song also reflects upon itself in both reality and within the piece itself.\nThe more you listen to it, the more you become convinced that the narrator and Willie are the same person.\nWithin the song the narrator, Willie, and the Gypsy are reflections of each other.\nLike Escher is an extension of his art, and his art an extension of him.\nAfter listening to the song hundreds of times, it becomes obvious that Willie is a manifestation of the narrator, an imaginary friend, a hallucination.\nHe is a character invented by the narrator to describe a corner of his personality.</p>\n<p>That's just one angle to look at it.</p>\n<p>It gets even more interesting when when you study how this song's reflections bleed into reality.\nHow Willie is a nickname for William, just like Billie.\nBillie Joe Shaver wrote this song, and he wrote it in the first person.\nBut Willie, is a clear reflection of Billie.</p>\n<p>Just like Willie, Shaver was salt of the Texas soil.\nHe was deadset on riding in the big rodeo, but he had to work some side gigs to make cash.\nLost two of his fingers when working in a lumber yard, but still managed to played guitar and write songs.\nAs it turns out, the songs where pretty damn good.</p>\n<p>Before Waylon Jennings recorded an album of his songs, \"Honky Tonk Heroes\", Shaver was unknown.\nLegend has it that Shaver played the song for Jennings backstage at a concert, but then Jennings forgot.\nIn Nashville a few months later, Shaver heard Jennings was drinking with some bikers at a local bar.\nHe confronted Jennings at the bar, and was asked to play the song again.\nHe played \"<a href=\"/songs/willie-the-wandering-gypsy\">Willie the Wandering Gypsy and Me</a>\", \"Old Five &#x26; Dimers Like Me\", and \"Aint No God In Mexico\".\nJennings then agreed to record an entire album of Shavers songs, and this time he kept his word.</p>\n<p>Its noteworthy to consider that both times Jennings asked Shaver to play a song, Shaver chose \"<a href=\"/songs/willie-the-wandering-gypsy\">Willie the Wandering Gypsy and Me</a>\".\nEspecially because \"Old Five &#x26; Dimers Like Me\" is undoubtedly a more approachable / sellable song.\nBut, its undeniable that Waylon, just like Shaver is a reflection of \"[Willie the Wander Gypsy]\".</p>\n<p>Although they had different stories, Shaver and Jennings where rolled from the same makings.\nJennings was a Blue Texas Northerner that became a performer because he couldn't stomach the idea of working for a living.\nHe had four wives and seven kids, but that never stopped him from living hard.\nOn Honky Tonk Heroes, Jennings delivers the songs with an authenticity that gives Shaver's performances a hard run for their money.</p>\n<p>Waylon Jennings has a voice that reverberates the strings of your soul.\nThe performance creates a clarity and intrigue that just doesn't come out when Shaver sings it.\nMaybe Shaver knew this, and that's why he sought out Jennings.\nWaylon Jennings' voice is itself a reflection of the song.\nIts like jamming your head full of figures and angles, but telling us stuff that we already know.</p>","frontmatter":{"title":"Willie The Wandering Gypsy"},"fields":{"date":"2017-08-28"}}},"pathContext":{"slug":"/posts/2017-08-28-willie-the-wandering-gypsy/"}}

